import React, { Component } from 'react';
import { HashRouter, Route, BrowserRouter as Router, Switch } from 'react-router-dom';
// import { renderRoutes } from 'react-router-config';
import './App.scss';

const loading = () => <div className="animated fadeIn pt-3 text-center">Loading...</div>;

// Containers
const DefaultLayout = React.lazy(() => import('./containers/DefaultLayout'));

// Pages
const Login = React.lazy(() => import('./views/Pages/Login'));
const Register = React.lazy(() => import('./views/Pages/Register'));
const Page404 = React.lazy(() => import('./views/Pages/Page404'));
const Page500 = React.lazy(() => import('./views/Pages/Page500'));
const MyReport = React.lazy(() => import('./views/Configurations/ViewReport2'));
const ClientReport = React.lazy(() => import('./views/Configurations/ViewReport3'));
const AssignedReport = React.lazy(() => import('./views/Configurations/ViewReport4'));
const ClientUserReport = React.lazy(() => import('./views/Configurations/ViewReportUser'));

class App extends Component {

  render() {
    return (
      <HashRouter>
          <React.Suspense fallback={loading()}>
            <Switch>
              <Route exact path="/login" name="Login Page" component={Login} />
              <Route exact path="/register" name="Register Page" render={props => <Register {...props}/>} />
              <Route exact path="/client_report" name="ViewReport Page" render={props => <MyReport {...props}/>} />
              <Route exact path="/view_report" name="Client Report Page" render={props => <ClientReport {...props}/>} />
              <Route exact path="/assigned_report" name="Assigned Report Page" render={props => <AssignedReport {...props}/>} />
              <Route exact path="/report" name="Report Page" render={props => <ClientUserReport {...props}/>} />
              <Route exact path="/404" name="Page 404" render={props => <Page404 {...props}/>} />
              <Route exact path="/500" name="Page 500" render={props => <Page500 {...props}/>} />
              <Route path="/" name="Home" render={props => <DefaultLayout {...props}/>} />
            </Switch>
          </React.Suspense>
      </HashRouter>
    );
  }
}

export default App;
